/********* margin & padding  **********/
.sidebaropen {
  margin-left: 73px; }

.sidebarclosed {
  margin-left: 300px; }

.collapse-tab-content .table thead th {
  color: #808697; }

.edit-user {
  margin-top: 10px; }
  .edit-user img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 1px solid gray; }
  .edit-user.profile {
    width: 80px;
    margin: 20px auto;
    position: relative; }
    .edit-user.profile label {
      position: absolute;
      bottom: 0;
      right: 2px;
      background: #5077f2;
      color: #fff;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      font-size: 12px;
      line-height: 25px;
      margin-bottom: 0; }
    .edit-user.profile input {
      display: none; }

.custom-ui {
  text-align: center;
  width: 500px;
  padding: 40px;
  background: #1d2027;
  box-shadow: 0 20px 75px rgba(0, 0, 0, 0.23);
  color: #aab4bd; }

.custom-ui > h1 {
  margin-top: 0; }

.custom-ui > button {
  width: 160px;
  padding: 10px;
  border: 1px solid #aab4bd;
  margin: 10px;
  cursor: pointer;
  background: none;
  color: #aab4bd;
  font-size: 14px; }

.description {
  font-size: 20px; }

.patient-icon, .badge-primary {
  font-size: 18px; }

.table th {
  font-size: 15px; }

.table td {
  font-size: 16px; }

.post-info {
  font-size: 20px; }

.card-header h2 {
  font-size: 24px; }

.form-group label {
