
html,
body {
	background: #ecedf0;
	width: 100%;
}

html {
	overflow-x: hidden !important;
	overflow-y: scroll !important;
}

body {
	color: #777;
	font-family: "Open Sans", Arial, sans-serif;
	line-height: 22px;
	margin: 0;
	font-size: 13px;
}

a {
	color: #CCC;
}

a:hover, a:focus {
	color: #d9d9d9;
}

a:active {
	color: #bfbfbf;
}

/* Layout Base - Main Wrapper	*/
.body {
	min-height: 100vh;
	width: 100%;
}

/* Layout Base - Header */
.header {
	height: 60px;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
}

/* Layout Base - Inner Wrapper */
.inner-wrapper {
	display: table;
	min-height: 100vh;
	padding-top: 60px;
	table-layout: fixed;
	overflow: hidden;
	width: 100%;
}

/* Layout Base - Content Body */
.content-body {
	display: table-cell;
	padding: 85px 40px;
	position: relative;
	vertical-align: top;
}

.content-body:not(.card-margin) > .row + .row {
	padding-top: 40px;
}

@media (max-width: 767px) {
	.content-body:not(.card-margin) > .row + .row {
		padding-top: 15px;
	}
}

.content-body.card-margin {
	padding-top: 0;
}

.content-body.card-margin .page-header {
	margin-bottom: 5px;
}

.content-body.card-margin .card {
	margin-top: 40px;
}

@media (max-width: 767px) {
	.content-body.card-margin .card {
		margin-top: 15px;
	}
}

/* Layout Base - Page Header */
.page-header {
	background: #171717;
	border-bottom: none;
	border-left: 1px solid #3A3A3A;
	box-shadow: 1px 3px 0 1px #CCCCCC;
	height: 50px;
	margin: -40px -40px 40px -40px;
	padding: 0;
}

/* Layout Base - Sidebar Left */
.sidebar-left {
	color: #777;
	display: table-cell;
	position: relative;
	vertical-align: top;
	width: 300px;
	z-index: 1010;
}

/* Layout Base - Sidebar Left Opened ( Larger than mobile ) */
@media only screen and (min-width: 768px) {
	html.sidebar-left-collapsed .sidebar-left {
		width: 73px;
	}
}
/* Layout Base - Sidebar Right */
.sidebar-right {
	background: #171717;
	bottom: 0;
	margin-right: -300px;
	min-height: 100vh;
	position: fixed;
	right: 0;
	top: 0;
	width: 300px;
}

/* Layout Base - Sidebar Right Opened ( Larger than mobile ) */
@media only screen and (min-width: 768px) {
	html.sidebar-right-opened .header {
		margin-left: -300px;
		margin-right: 300px;
	}

	html.sidebar-right-opened .inner-wrapper {
		margin-left: -300px;
	}

	html.sidebar-right-opened .sidebar-right {
		margin-right: 0;
	}

	html.sidebar-right-opened.has-top-menu:not(.has-left-sidebar-half) .inner-wrapper {
		margin-left: 0;
		padding-right: 300px;
	}
}
/* Layout Base - Sidebar Right Opened - Has Top Horizontal Menu ( Ipad ) */
@media only screen and (min-width: 768px) and (max-width: 991px) {
	html.sidebar-right-opened.has-top-menu:not(.has-left-sidebar-half) .content-body {
		right: 300px;
	}
}
/* Layout Base - Flexbox supported */
@media only screen and (min-width: 768px) {
	html.flexbox,
	html.flexboxlegacy {
		/* Tab Navigation ;*/
	}

	html.flexbox .inner-wrapper,
	html.flexboxlegacy .inner-wrapper {
		display: -webkit-flex;
		display: flex;
	}

	html.flexbox .sidebar-left,
	html.flexbox .content-body,
	html.flexboxlegacy .sidebar-left,
	html.flexboxlegacy .content-body {
		display: block;
		-webkit-flex-shrink: 0;
		        flex-shrink: 0;
	}

	html.flexbox .content-body,
	html.flexboxlegacy .content-body {
		-webkit-flex: 2 1;
		        flex: 2 1;
		min-width: 1px;
	}

	html.flexbox.has-tab-navigation .inner-wrapper,
	html.flexboxlegacy.has-tab-navigation .inner-wrapper {
		-webkit-flex-direction: column;
