}

.search-content > .tab-content {
	border: none;
	box-shadow: none;
	padding: 0 40px;
}

/* Search Result Totals */
.search-content .total-results {
	margin-top: -25px;
}

/* Search Results List */
.search-results-list {
	max-width: 750px;
}

.search-results-list li {
	border-bottom: 1px solid #EEE;
	margin-bottom: 15px;
	padding-bottom: 15px;
