	color: #FFF;
	font-size: 57.6px;
	font-size: 3.6rem;
	line-height: 120px;
	line-height: 7.5rem;
	text-align: center;
}

/* Search Results Responsive */
@media only screen and (max-width: 767px) {
	.search-content {
		margin: -20px;
	}
}

@media only screen and (max-width: 480px) {
