	content: '';
	display: block;
	height: 100%;
	left: 140px;
	margin-left: -2px;
	position: absolute;
	top: 0;
	width: 3px;
	z-index: 0;
	opacity: 0.35;
}

.timeline .tm-title {
	position: relative;
	display: inline-block;
	text-align: center;
	min-width: 200px;
	background-color: #fff;
	padding: 4px 5px;
	margin: 0 40px;
	z-index: 1;
	border-radius: 3px;
}

.timeline .tm-items {
	list-style: none;
	padding: 0;
	margin: 0;
}

.timeline .tm-items > li {
	position: relative;
	margin: 30px 0;
	padding: 0 0 0 190px;
	min-height: 65px;
	z-index: 1;
}

.timeline .tm-items > li .tm-datetime {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100px;
	height: 48px;
	margin-top: -24px;
	text-align: right;
	z-index: 3;
}

.timeline .tm-items > li .tm-datetime .tm-datetime-time {
	color: #CCC;
	font-size: 22.4px;
	font-size: 1.4rem;
	font-weight: 700;
	margin: 0;
	white-space: nowrap;
}

.timeline .tm-items > li .tm-icon {
	position: absolute;
	top: 50%;
	left: 140px;
	background-color: #ecedf0;
	border: 3px solid #CCC;
	color: #CCC;
	font-size: 28px;
	padding: 10px;
	width: 55px;
	height: 55px;
	text-align: center;
	line-height: 29px;
	margin-top: -28px;
	margin-left: -28px;
	z-index: 2;
	border-radius: 28px;
}

.timeline .tm-items > li .tm-box {
	position: relative;
	background: #fff;
	min-height: 65px;
	padding: 10px 20px;
	border: 1px solid #e9e9e9;
	border-radius: 6px;
}

.timeline .tm-items > li .tm-box:after {
	right: 100%;
	border: solid transparent;
	content: ' ';
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-right-color: #fff;
	border-width: 8px;
	top: 50%;
	margin-top: -8px;
	z-index: 2;
}

.timeline .tm-items > li .tm-box p:last-child {
	margin-bottom: 0;
}

.timeline .tm-items > li .tm-box .tm-meta {
	margin: 10px 0 0;
}

.timeline .tm-items > li .tm-box .tm-meta span {
	display: inline-block;
	padding-right: 8px;
}

.timeline .tm-items > li .tm-box .tm-meta span:last-child, .timeline .tm-items > li .tm-box .tm-meta span:last-of-type {
	padding-right: 0;
}

.timeline.timeline-simple .tm-body:after {
	left: 30px;
}

.timeline.timeline-simple .tm-body .tm-title {
	border: 1px solid #e9e9e9;
	margin: 0 10px;
}

.timeline.timeline-simple .tm-body .tm-items > li {
	padding: 0 0 0 55px;
}

.timeline.timeline-simple .tm-body .tm-items > li:before {
	display: block;
	position: absolute;
	content: ' ';
	background: none repeat scroll 0 0 #CCC;
	border-radius: 50%;
	box-shadow: 0 0 0 3px #FFF, 0 0 0 6px #CCC;
	height: 7px;
	left: 30px;
	top: 50%;
	width: 8px;
	margin-left: -4px;
	margin-top: -4px;
}

.timeline.timeline-simple .tm-body .tm-items > li .tm-box:before {
	left: -17px;
	border: solid transparent;
	content: ' ';
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-right-color: #e9e9e9;
	border-width: 8px;
	top: 50%;
	margin-top: -8px;
	z-index: 1;
}

